article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.618;
  font-family: Arial, Helvetica, sans-serif;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

code,
pre,
samp {
  font-family: monospace;
}

img,
video {
  max-width: 100%;
  vertical-align: middle;
}

p {
  margin: 0 0 1em;
}

h1,
h2,
h3,
h4 {
  margin: 0.5em 0 0.75em;
  line-height: 1.3;
}

h1 {
  font-size: 1.5em;
}

h2 {
  font-size: 1.25em;
}

h3 {
  font-size: 1.125em;
}

h4 {
  font-size: 1em;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.cf:after,
.lab-grid:after {
  content: "";
  display: table;
  clear: both;
}

.wrapper {
  margin-left: auto;
  margin-right: auto;
  padding-right: 1.5em;
  padding-left: 1.5em;
  max-width: 1080px;
}

.icon {
  height: 1em;
  width: 1em;
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
}

.buttons {
  margin: 1.5em 0;
}

.button {
  padding: 1em 1.5em;
  color: rgba(255, 255, 255, 0.75);
  border-radius: 3px;
}

.button:hover {
  color: rgba(255, 255, 255, 0.9);
}

.green-sea {
  background: #16a085;
}

.button.green-sea:hover {
  background: #1abc9c;
}

.belize-hole {
  background: #2980b9;
}

.button.belize-hole:hover {
  background: #3498db;
}

.lab-nav {
  width: 100%;
}

.lab-nav--fixed {
  position: fixed;
}

.lab-nav--top {
  top: 0;
}

.lab-nav--bottom {
  bottom: 0;
}

.lab-nav,
.lab-follow {
  font-size: 13px;
  line-height: 1;
}

.lab-theme-light body {
  color: rgba(0, 0, 0, 0.75);
}

.lab-theme-light .lab-nav,
.lab-theme-light .lab-follow {
  background: rgba(0, 0, 0, 0.05);
}

.lab-theme-light .lab-nav,
.lab-theme-light .lab-follow,
.lab-theme-light .lab-nav a {
  color: rgba(0, 0, 0, 0.35);
}

.lab-theme-light .lab-nav a:hover,
.lab-theme-light .lab-nav a:active,
.lab-theme-light .lab-nav a:focus,
.lab-theme-light .lab-follow:hover,
.lab-theme-light .lab-follow:active,
.lab-theme-light .lab-follow:focus {
  background: rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.5);
}

.lab-theme-light .lab-article-button {
  color: rgba(0, 0, 0, 0.25);
  border: 3px solid rgba(0, 0, 0, 0.1);
}

.lab-theme-light .lab-article-button:hover,
.lab-theme-light .lab-article-button:active,
.lab-theme-light .lab-article-button:focus {
  color: rgba(0, 0, 0, 0.3);
  border-color: rgba(0, 0, 0, 0.15);
}

.lab-theme-dark body {
  color: rgba(255, 255, 255, 0.75);
}

.lab-theme-dark .lab-nav,
.lab-theme-dark .lab-follow {
  background: rgba(255, 255, 255, 0.05);
}

.lab-theme-dark .lab-nav,
.lab-theme-dark .lab-nav a,
.lab-theme-dark .lab-follow {
  color: rgba(255, 255, 255, 0.35);
}

.lab-theme-dark .lab-nav a:hover,
.lab-theme-dark .lab-nav a:active,
.lab-theme-dark .lab-nav a:focus,
.lab-theme-dark .lab-follow:hover,
.lab-theme-dark .lab-follow:active,
.lab-theme-dark .lab-follow:focus {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.5);
}

.lab-theme-dark .lab-article-button {
  color: rgba(255, 255, 255, 0.25);
  border: 3px solid rgba(255, 255, 255, 0.1);
}

.lab-theme-dark .lab-article-button:hover,
.lab-theme-dark .lab-article-button:active,
.lab-theme-dark .lab-article-button:focus {
  color: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.15);
}

.lab-follow,
.lab-article-link,
.lab-share a {
  padding: 1.25em 0.75em;
  display: inline-block;
  vertical-align: bottom;
  text-decoration: none;
}

.lab-follow,
.lab-article-link span,
.lab-share-link span {
  letter-spacing: 1px;
  text-transform: uppercase;
}

.lab-follow {
  display: none;
}

.lab-follow .icon {
  vertical-align: top;
}

.lab-article-link {
  float: left;
}

.lab-share {
  float: right;
  text-align: right;
}

.lab-share ul {
  margin: 0;
}

.lab-share li {
  display: inline;
  margin: 0;
}

.lab-article-button {
  font-weight: 700;
  display: block;
  margin: 2em 0;
  padding: 1.25em;
  text-align: center;
}

.lab-grid {
  width: 100%;
}

.lab-grid .lab-grid-cell {
  margin-bottom: 1.5em;
}

.lab-grid .lab-grid-cell--mb-2x {
  margin-bottom: 3em;
}

.lab-grid-cell {
  padding-left: 0;
}

.w-full {
  width: 100%;
}

.w-three-quarters {
  width: 75%;
}

.w-two-thirds {
  width: 66.66666%;
}

.w-half {
  width: 50%;
}

.w-third {
  width: 33.33333%;
}

.w-quarter {
  width: 25%;
}

.labs-pagination {
  display: flex;
  justify-content: center;
}
.labs-pagination span,
.labs-pagination a {
  vertical-align: middle;
  margin: 0.5em;
}

.labs-pagination span a {
  padding: 0.5em 1em;
}

@media only screen and (min-width: 1024px) {
  h1 {
    font-size: 2em;
  }
  h2 {
    font-size: 1.65em;
  }
  h3 {
    font-size: 1.25em;
  }
  h4 {
    font-size: 1em;
  }

  .w-full--d {
    width: 100%;
  }

  .w-three-quarters--d {
    width: 75%;
  }

  .w-two-thirds--d {
    width: 66.66666%;
  }

  .w-half--d {
    width: 50%;
  }

  .w-third--d {
    width: 33.33333%;
  }

  .w-quarter--d {
    width: 25%;
  }
}

@media only screen and (max-width: 767px) {
  .lab-article-link span,
  .lab-share span {
    display: none;
  }
}

@media only screen and (min-width: 1024px) {
  .lab-follow {
    display: inline-block;
    position: fixed;
    right: -34px;
    bottom: 20%;
    transform: rotate(-90deg);
  }
}

@media only screen and (min-width: 768px) {
  .lab-grid {
    margin-left: -1.5em;
    width: calc(100% + 1.5em);
  }

  .lab-grid-cell {
    float: left;
    padding-left: 1.5em;
  }
}

@media only screen and (max-width: 1023px) and (min-width: 768px) {
  h1 {
    font-size: 1.75em;
  }
  h2 {
    font-size: 1.45em;
  }

  .lab-grid--3 .lab-grid-cell {
    width: 50%;
  }

  .w-full--t {
    width: 100%;
  }

  .w-three-quarters--t {
    width: 75%;
  }

  .w-two-thirds--t {
    width: 66.66666%;
  }

  .w-half--t {
    width: 50%;
  }

  .w-third--t {
    width: 33.33333%;
  }

  .w-quarter--t {
    width: 25%;
  }
}

@media only screen and (max-width: 767px) {
  .lab-share {
    text-align: right;
  }

  .w-full--m {
    width: 100%;
  }

  .w-three-quarters--m {
    width: 75%;
  }

  .w-two-thirds--m {
    width: 66.66666%;
  }

  .w-half--m {
    width: 50%;
  }

  .w-third--m {
    width: 33.33333%;
  }

  .w-quarter--m {
    width: 25%;
  }
}
